<template>
  <div class="container">
    <h1>Ultimos Carros</h1>
    <div class="create-post">
      <label for="modelo">Modelo</label>
      <input type="text" id="modelo" v-model="modelo" >
      <label for="placa">Placa</label>
      <input type="text" id="placa" v-model="placa" >
      <button v-on:click="saveCarro">Cadastrar</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="posts-container">
      <div class="post"
        v-for="(item, index) in carros"
        v-bind:item="item"
        v-bind:index="index"
        v-bind:key="item.codigo"
        v-on:dblclick="deleteCarro(item.codigo)"
      >
        <p class="text">{{item.modelo}}</p>
        <p class="text">{{item.placa}}</p>
      </div>
    </div>

  </div>
</template>

<script>
import CarroService from '../CarroService';
export default {
  name: 'CarrosComponent',
  data(){
    return {
      carros: [],
      error: '',
      modelo: '',
      placa: '',
    }
  },
  mounted(){
    this.getCarros();
  },
  methods:{
    async getCarros(){
      try {
        let getCarros = CarroService.getCarros();
        getCarros.then(total => {
          this.carros = total
        })
      } catch (error) {
        this.error = error.message;
      }
    },
    async saveCarro(){
      await CarroService.insertCarro(this.modelo, this.placa);
      this.getCarros();
    },
    async deleteCarro(id){
      await CarroService.deleteCarro(id);
      this.getCarros();
    }
  }
}
</script>

