import axios from 'axios';

const url = '/api'

class CarroService {
    static getCarros(){
        return new Promise ((resolve, reject) =>{
            try {
                axios.get(url + '/carros')
                    .then(function(response){
                        const data = response.data;
                        resolve(
                            data.map(carro => ({
                                ...carro,
                            }))
                        );
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            } catch (err) {
                reject(err);
            }
        });
    }

    static insertCarro(modelo, placa){
        const params = new URLSearchParams()
        params.append('modelo', modelo)
        params.append('placa', placa)
        const config = {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }
        return axios.post(url+'/carro', params, config)
        .then((result) => {
          console.log(result)
        })
        .catch((err) => {
            console.log(err)
        })
    }

    static deleteCarro(id){
        return axios.delete(`${url}/carro/${id}/delete`)
    }
}

export default CarroService;